/* stylelint-disable selector-max-type */

.div-startup-info {
	width: 320px;
}

.div-startup-name {
	color: gray;
	font-weight: 500;
	font-size: 24px;
}

.div-startup-tagline {
	margin-bottom: 12px;
	font-style: italic;
	font-size: 12px;
}

.div-startup-description {
	margin-bottom: 24px;
	font-size: 18px;
}

.div-label {
	margin-top: 24px;
	color: lightgray;
	font-weight: 300;
	font-size: 12px;
}

.img-startup-info {
	max-height: 32px;
}

a {
	color: black;
	text-decoration: none;
}

a:hover {
	text-decoration: none;
	opacity: 0.2;
}

.icon-close {
	position: relative;
	top: 0;
	right: 0;
	float: right;
}

.icon-close:hover {
	color: orange;
	cursor: pointer;
}

.a-lint-item {
	position: relative;
	top: -9px;
	margin-left: 12px;
}

.div-link-item {
	vertical-align: middle;
	white-space: nowrap;
}
